
.obch {
  display: flex;
}
.icon {
  width: 30px;
  height: 30px;
  border: 1px solid black;
  border-radius: 5px;
}
.img1 {
  width: 100%;
  height: 100%;
}  
.line_search {
  height: 30px;
  width: 0;
  padding: 0;
  overflow: hidden;
  transition: all 0.5s;
  display: flex;
}
.icon:hover+.line_search {
  width: 300px;
  transition: all 0.5s;
}
.rodit {
  position: relative;
  margin-top: 100px;
  margin-left: 100px;
  width: 200px;
  height: 200px;
  
 
  overflow: hidden;
}
.reb1 {
  position: absolute;
  top: 0px;
  left: 0;
  width: 100%;
  height: 100%;
  transform: rotate(45deg);
  background-color: blueviolet;
}
